import Vue from 'vue'
import Vuex from 'vuex'
import VuexPersistence from 'vuex-persist'
import * as axios from 'axios'

Vue.use(Vuex);

const vuexLocal = new VuexPersistence({
  storage: window.localStorage
});

export default new Vuex.Store({
  state: {
    result: {
      mbti: null,
      bf: null,
      sent: false
    },
    personalities: [
      { title: 'Architecte', desc: 'On se sent seul en haut et, comme ils sont un des types de personnalité les plus rares et les plus capables sur le plan stratégique, les Architectes ne le savent que trop bien. Les Architectes forment juste deux pourcent de la population et les femmes de ce type de personnalité sont particulièrement rares, car elles ne forment que 0,8 % de la population. Pour les Architectes, il est souvent difficile de trouver des individus de même sensibilité qui soient capables de suivre leur implacable intellectualisme et leurs manoeuvres de joueurs d’échec. Les gens qui ont le type de personnalité « Architecte » sont imaginatifs mais catégoriques, ambitieux et pourtant secrets, étonnamment curieux, mais ils ne gaspillent pas leur énergie.', img: 'intj-architect', sigle: 'INTJ' },
      { title: 'Logicien', desc: 'Le type de personnalité « Logicien » est assez rare, car il ne constitue que trois pourcent de la population, ce qui est incontestablement une bonne chose pour eux, car rien ne les attristerait plus que d’être « ordinaires ». Les Logiciens s’enorgueillissent de leur inventivité et de leur créativité, de leur perspective hors du commun et de la vigueur de leur intellect. Habituellement connus sous les traits du philosophe, de l’architecte ou du professeur rêveur, les Logiciens ont été à l’origine de nombreuses découvertes scientifiques tout au long de l’histoire.', img: 'intp-logician', sigle: 'INTP' },
      { title: 'Commandant', desc: 'Les Commandants sont des leaders nés. Les gens qui ont ce type de personnalité incarnent les dons du charisme et de la confiance, et dégagent de l’autorité d’une manière qui rassemble les foules sous l’égide d’un but commun. Cependant, à la différence de leurs homologues plus sentimentaux, les Commandants se caractérisent par un niveau de rationalité souvent implacable. Ils utilisent leur dynamisme, leur détermination et leur esprit vif pour accomplir le but qu’ils se sont fixé, quel qu’il soit. Ils ne représentent que trois pourcent de la population et c’est peut-être une bonne chose car, autrement, ils pourraient écraser les types de personnalité plus timides et sensibles qui constituent une grande partie du reste du monde ; cela dit, il nous faut remercier les Commandants pour avoir créé beaucoup des entreprises et des institutions que nous considérons quotidiennement comme allant de soi.', img: 'entj-commander', sigle: 'ENTJ' },
      { title: 'Innovateur', desc: 'Le type de personnalité « Innovateur » est l’avocat du diable ultime qui prospère en décomposant les arguments et les croyances et en laisse les lambeaux flotter au vent pour que tous puissent les voir. Les Innovateurs ne le font pas parce qu’ils essaient d’atteindre un objectif plus profond ou un but stratégique, mais pour la simple raison que c’est amusant. Personne n’aime plus le processus de lutte mentale que les Innovateurs, car il leur donne l’occasion d’exercer leur bel esprit rapide et fluide, la large somme de connaissances qu’ils ont accumulée et la capacité de relier entre elles des idées disparates pour prouver qu’ils ont raison.', img: 'entp-debater', sigle: 'ENTP' },
      { title: 'Avocat', desc: 'Le type de personnalité « Avocat » est très rare et constitue moins de un pourcent de la population, mais cela ne les empêche pas de laisser leur empreinte sur le monde. Ils ont un sens inné de l’idéalisme et de la morale, mais leur détermination et leur fermeté les différencie des autres types de personnalité. Les Avocats ne sont pas des rêveurs inactifs, mais des gens capables de prendre des mesures concrètes pour réaliser leurs buts et laisser un impact durablement positif.', img: 'infj-advocate', sigle: 'INFJ' },
      { title: 'Médiateur', desc: 'Les Médiateurs sont de vrais idéalistes qui recherchent toujours un soupçon de bien, même chez les pires personnes ou dans les pires évènements, et recherchent des moyens d’améliorer les choses. Bien qu’ils puissent être perçus comme étant des gens calmes, réservés ou même timides, les Médiateurs ont une flamme et une passion intérieures qui peuvent vraiment briller. Comme ils ne constituent que 4 % de la population, le risque de se sentir incompris est malheureusement élevé pour les gens du type de personnalité « Médiateur », mais s’ils trouvent des gens de même sensibilité qu’eux avec qui passer leur temps, l’harmonie qu’ils ressentiront sera une fontaine de joie et d’inspiration.', img: 'infp-mediator', sigle: 'INFP' },
      { title: 'Protagoniste', desc: 'Les Protagonistes sont des leaders nés, pleins de passion et de charisme. Ils constituent environ deux pourcent de la population et sont souvent nos politiciens, nos coachs et nos enseignants. Ils tendent la main aux autres et les inspirent pour qu’ils accomplissent et fassent le bien dans le monde. Avec une confiance naturelle qui génère de l’influence, les Protagonistes ont beaucoup de fierté et de joie à aider les autres à travailler ensemble pour s’améliorer et améliorer leur communauté.', img: 'enfj-protagonist', sigle: 'ENFJ' },
      { title: 'Inspirateur', desc: 'La personnalité « Inspirateur » est un véritable esprit libre. Ils sont souvent l’âme d’une soirée mais, à la différence des Explorateurs, ils sont moins intéressés par l’excitation et le plaisir purs du moment présent que par l’appréciation des connexions sociales et émotionnelles qu’ils établissent avec autrui. Charmants, indépendants, énergiques et compatissants, les 7 % de la population qu’ils représentent peuvent certainement se repérer dans n’importe quelle foule.', img: 'enfp-campaigner', sigle: 'ENFP' },
      { title: 'Logisticien', desc: 'On considère le type de personnalité « Logisticien » comme le plus courant, car il constitue environ 13 % de la population. Leurs caractéristiques intrinsèques d’intégrité, de logique pragmatique et d’infatigable dévouement envers le devoir font des Logisticiens le coeur vital de nombreuses familles, aussi bien que des organisations qui soutiennent les traditions, les règles et les normes comme les cabinets d’avocat, les organismes de réglementation et les militaires. Les gens qui ont le type de personnalité « Logisticien » aiment être responsables de leurs actions et s’enorgueillissent du travail qu’ils font. Quand ils travaillent pour atteindre un but, les Logisticiens ne comptent pas leur temps et leur énergie pour achever chaque tâche pertinente avec exactitude et patience.', img: 'intp-logician', sigle: 'INTP' },
      { title: 'Défenseur', desc: 'Le type de personnalité des Défenseurs est tout à fait unique, car beaucoup de leurs qualités défient toute définition de leurs traits individuels. Bien que compréhensifs, les Défenseurs peuvent être farouches lorsqu’ils doivent protéger leur famille ou leurs amis. Bien que calmes et réservés, ils ont un sens solide du contact humain et des relations humaines approfondies. Bien qu’ils recherchent la stabilité et la sécurité, ils sont souvent réceptifs au changement et aux nouvelles idées. Comme tant de choses, les gens qui ont le type de personnalité « Défenseur » sont plus que la somme de leurs parties et c’est la manière dont ils utilisent ces points forts qui définit qui ils sont.', img: 'isfj-defender', sigle: 'ISFJ' },
      { title: 'Directeur', desc: 'Les Directeurs sont les représentants de la tradition et de l’ordre. Ils utilisent leur compréhension de ce qui est bien, mal et socialement acceptable pour rassembler les familles et les communautés. Les gens qui ont le type de personnalité « Directeur » adoptent les valeurs d’honnêteté, de dévouement et de dignité, sont appréciés pour la clarté de leurs conseils et heureux de montrer la voie quand la route est difficile. Les Directeurs sont fiers de rassembler les gens. Ils assument souvent le rôle d’organisateurs communautaires et travaillent dur pour rassembler tout le monde dans le cadre de la célébration d’évènements locaux qui leur sont chers ou de la défense des valeurs traditionnelles qui maintiennent la cohésion des familles et des communautés.', img: 'estj-executive', sigle: 'ESTJ' },
      { title: 'Consul', desc: 'Les gens qui partagent le type de personnalité « Consul » sont, faute d’un meilleur terme, populaires, ce qui est logique, vu que c’est aussi un type de personnalité très répandu qui représente douze pourcent de la population. Au lycée, les Consuls sont les majorettes et les quarterbacks, ceux qui donnent le ton, captent la lumière des projecteurs et mènent leur équipe vers la victoire et la célébrité. Plus tard dans leur vie, les Consuls aiment encore soutenir leurs amis et ceux qu’ils aiment, organiser des rencontres sociales et faire de leur mieux pour s’assurer que tout le monde est heureux.', img: 'esfj-consul', sigle: 'ESFJ' },
      { title: 'Virtuose', desc: 'Les Virtuoses adorent explorer des mains et des yeux, toucher et examiner le monde qui les entoure avec un rationalisme calme et une curiosité pleine d’allant. Les gens qui ont ce type de personnalité sont des Faiseurs nés qui évoluent d’un projet à un autre, construisent des choses utiles et des choses superflues pour le plaisir et apprennent de leur environnement au fur et à mesure. Souvent mécaniciens et ingénieurs, le plus grand plaisir des Virtuoses est de se salir les mains en démontant les choses et en les remontant un petit peu mieux qu’elles n’étaient auparavant.', img: 'istp-virtuoso', sigle: 'ISTP' },
      { title: 'Aventurier', desc: 'Les types de personnalité « Aventurier » sont de vrais artistes, mais pas forcément dans le sens typique où ils sortiraient peindre de joyeux petits arbres. Cependant, assez souvent, ils en sont parfaitement capables. Ils ont plutôt tendance à utiliser l’esthétique, le design et même leurs propres choix et actions pour repousser les limites des conventions sociales. Les Aventuriers aiment bouleverser les attentes traditionnelles avec des expériences sur la beauté et le comportement. Il y a des chances pour qu’ils aient plus d’une fois exprimé l’idée : « Ne me rangez pas dans un tiroir ! »', img: 'isfp-adventurer', sigle: 'ISFP' },
      { title: 'Entrepreneur', desc: 'Les types de personnalité « Entrepreneur » ont toujours un impact sur leur environnement immédiat. La meilleure façon de les repérer lors d’une fête est de chercher le tourbillon de gens qui évoluent autour d’eux alors qu’ils passent de groupe en groupe. Riantes et distrayantes avec un humour brusque et truculent, les Entrepreneurs aiment attirer l’attention. Si on demande à un membre du public de monter sur scène, les Entrepreneurs se portent volontaires, ou désignent un ami timide comme volontaire.', img: 'estp-entrepreneur', sigle: 'ESTP' },
      { title: 'Amuseur', desc: 'Si une personne est susceptible de se mettre spontanément à chanter et à danser, c’est le type de personnalité « Amuseur ». Les Amuseurs se laissent emporter par l’excitation du moment présent et veulent que tous les autres ressentent les choses de la même façon. Aucun autre type de personnalité n’est aussi généreux avec son temps et son énergie que les Amuseurs quand il s’agit d’encourager les autres, et aucun autre type de personnalité ne le fait avec un style aussi irrésistible.', img: 'esfp-entertainer', sigle: 'ESFP' }
    ]
  },
  getters: {
    getMbtiResult(state) {
      return state.result.mbti;
    },
    getBfResult(state) {
      return state.result.bf;
    },
    getPersonalities(state) {
      return state.personalities;
    },
    isMobileDevice() {
      return window.innerWidth < 1000;
    }
  },
  mutations: {
    setMbtiResult(state, mbti) {
      state.result.mbti = mbti.toUpperCase();
    },
    setBfResult(state, bf) {
      state.result.bf = bf;
    }
  },
  actions: {
    sendProfileToDB({ state }) {
      if (state.result.sent === true || !state.result.mbti || !state.result.bf)
        return;

      axios.post('http://localhost:5000/insert', {
        mbti: state.result.mbti,
        bf: state.result.bf
      }).then(res => {
        console.log(res);
        state.result.sent = true;
      }).catch(err => {
        console.log(err);
      });
    }
  },
  plugins: [vuexLocal.plugin]
});
